<template><section>
<h1>多选框</h1>
<p>一组备选项中进行多选</p>
<h2 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h2>
<demo-block>
                            <xl-checkbox v-model="value" label="选择框"></xl-checkbox>
<p>{{value}}</p>

                            <div slot="desc"><p>基础使用方式</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zi-ding-yi-zhi"><a class="header-anchor" href="#zi-ding-yi-zhi" aria-hidden="true">¶</a> 自定义值</h2>
<demo-block>
                            <xl-checkbox v-model="value2" true-value="TRUE" false-value="FALSE" label="选择框"></xl-checkbox>
<p>{{value2}}</p>

                            <div slot="desc"><p>使用<code>true-value</code>和<code>false-value</code>自定义值</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">true-value</span>=<span class="hljs-string">'TRUE'</span> <span class="hljs-attr">false-value</span>=<span class="hljs-string">'FALSE'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="jin-yong"><a class="header-anchor" href="#jin-yong" aria-hidden="true">¶</a> 禁用</h2>
<demo-block>
                            <xl-checkbox v-model="value" disabled label="选择框"></xl-checkbox>
<p>{{value}}</p>

                            <div slot="desc"><p>使用<code>disabled</code>禁用多选框</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zhi-du"><a class="header-anchor" href="#zhi-du" aria-hidden="true">¶</a> 只读</h2>
<demo-block>
                            <xl-checkbox v-model="value" readonly label="选择框"></xl-checkbox>
<p>{{value}}</p>

                            <div slot="desc"><p>使用<code>readonly</code>只读多选框</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">readonly</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="bian-kuang"><a class="header-anchor" href="#bian-kuang" aria-hidden="true">¶</a> 边框</h2>
<demo-block>
                            <xl-checkbox v-model="value" border="" label="选择框"></xl-checkbox>
<p>{{value}}</p>

                            <div slot="desc"><p>使用<code>border</code>给多选框设置边框</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">border</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'选择框'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="duo-xuan-kuang-zu"><a class="header-anchor" href="#duo-xuan-kuang-zu" aria-hidden="true">¶</a> 多选框组</h2>
<demo-block>
                            <xl-checkbox-group v-model="arr">
    <xl-checkbox label="123"></xl-checkbox>
    <xl-checkbox value="456"></xl-checkbox>
    <xl-checkbox value="987" label="789"></xl-checkbox>
</xl-checkbox-group>
<p>{{arr}}</p>

<xl-checkbox-group v-model="arr2">
    <xl-checkbox label="123"></xl-checkbox>
    <xl-checkbox value="456"></xl-checkbox>
    <xl-checkbox value="987" label="789"></xl-checkbox>
</xl-checkbox-group>
<p>{{arr2}}</p>

                            <div slot="desc"><p>使用<code>xl-checkbox-group</code>实现多选框组</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'456'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'987'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'789'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr2'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'456'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'987'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'789'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="an-niu-yang-shi-de-duo-xuan-kuang-zu"><a class="header-anchor" href="#an-niu-yang-shi-de-duo-xuan-kuang-zu" aria-hidden="true">¶</a> 按钮样式的多选框组</h2>
<demo-block>
                            <xl-checkbox-group type="button" v-model="arr2">
    <xl-checkbox label="123">aaa</xl-checkbox>
    <xl-checkbox value="456">bbb</xl-checkbox>
    <xl-checkbox value="987" label="789">ccc</xl-checkbox>
</xl-checkbox-group>
<p>{{arr2}}</p>

                            <div slot="desc"><p>使用<code>type=&quot;button&quot;</code>实现多选框组</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'button'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr2'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span>&gt;</span>aaa<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'456'</span>&gt;</span>bbb<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'987'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'789'</span>&gt;</span>ccc<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="indeterminate-zhuang-tai"><a class="header-anchor" href="#indeterminate-zhuang-tai" aria-hidden="true">¶</a> indeterminate 状态</h2>
<demo-block>
                            <xl-checkbox :indeterminate="indeterminate" :value="checkAll">全选</xl-checkbox>
<xl-checkbox-group type="button" v-model="arr2">
    <xl-checkbox label="123">aaa</xl-checkbox>
    <xl-checkbox value="456">bbb</xl-checkbox>
    <xl-checkbox value="987" label="789">ccc</xl-checkbox>
</xl-checkbox-group>
<p>{{arr2}}</p>

                            <div slot="desc"><p>使用<code>indeterminate</code>实现多选框组</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"indeterminate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">'checkAll'</span>&gt;</span>全选<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox-group</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'button'</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'arr2'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'123'</span>&gt;</span>aaa<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'456'</span>&gt;</span>bbb<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">value</span>=<span class="hljs-string">'987'</span> <span class="hljs-attr">label</span>=<span class="hljs-string">'789'</span>&gt;</span>ccc<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{arr2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="checkbox-shu-xing"><a class="header-anchor" href="#checkbox-shu-xing" aria-hidden="true">¶</a> checkbox 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>checkbox 显示值</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>checkbox 的 name（单独使用时有效）</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>checkbox 单独使用时，支持 v-model 绑定</td>
<td>string / number / boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>true-value</td>
<td>选中时的 value 值（单独使用时有效）</td>
<td>string / number / boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>false-value</td>
<td>未选中时的 value 值（单独使用时有效）</td>
<td>string / number / boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>border</td>
<td>是否显示 checkbox 边框</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否只读</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>半选状态 (一旦为 true，多选框就不能为其他状态)</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-shi-jian"><a class="header-anchor" href="#checkbox-shi-jian" aria-hidden="true">¶</a> checkbox 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>checkbox 改变事件</td>
<td>选中时的值（trueValue）</td>
</tr>
<tr>
<td>on-focus</td>
<td>checkbox 获得焦点事件</td>
<td>表单元素事件 e</td>
</tr>
<tr>
<td>on-blur</td>
<td>checkbox 失去焦点事件</td>
<td>表单元素事件 e</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-slots"><a class="header-anchor" href="#checkbox-slots" aria-hidden="true">¶</a> checkbox slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>checkbox 显示的 label，如果没有设置插槽，默认使用 label 参数，如果 label 也没有，就为空</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-group-shu-xing"><a class="header-anchor" href="#checkbox-group-shu-xing" aria-hidden="true">¶</a> checkbox group 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选中的 checkbox v-model 绑定</td>
<td>array</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>name</td>
<td>多选框选框组的名称 ，默认随机生成</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>多选框组类型</td>
<td>string</td>
<td>button</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-group-shi-jian"><a class="header-anchor" href="#checkbox-group-shi-jian" aria-hidden="true">¶</a> checkbox group 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>checkbox 选中事件</td>
<td>选中时的 checkbox 的值</td>
</tr>
</tbody>
</table>
<h2 id="checkbox-group-slots"><a class="header-anchor" href="#checkbox-group-slots" aria-hidden="true">¶</a> checkbox group slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>用来设置 checkbox 组</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default{
        data(){
            return {
                value:false,
                value2:'TRUE',
                arr:[],
                arr2:['123','456']
            }
        },
        computed:{
            indeterminate(){
                return this.arr2.length>0 && this.arr2.length<3
            },
            checkAll(){
                return this.arr2.length===3
            }
        }
    }
</script>


